import { useRoute, useRouter, createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import 'element-plus/es/components/message/style/css'
import 'element-plus/es/components/message-box/style/css'
import Home from '../components/home/Home.vue'
import Details from '../components/home/Details.vue'
import Kind from '../components/Kind.vue'
import Personal from '../components/personal/Personal.vue'
import Main from '../views/Main.vue'

import AllOrder from '../components/personal/Order/AllOrder.vue'
import Undelivered from '../components/personal/Order/Undelivered.vue'
import Delivered from '../components/personal/Order/Delivered.vue'
import Unpaid from '../components/personal/Order/Unpaid.vue'
import Received from '../components/personal/Order/Received.vue'
import UserInfo from '../components/personal/user/UserInfo.vue'
import Address from '../components/personal/user/Address.vue'

import Cart from '../components/cart/Cart.vue'
import Confirm from '../components/cart/Confirm.vue'
import Payment from '../components/cart/Payment.vue'
import PaySuccess from '../components/cart/PaySuccess.vue'

import Manager from '../manager_views/Manager.vue'
import User from '../manager_views/mg_components/User.vue'
import Banner from '../manager_views/mg_components/Banner.vue'
import Comment from '../manager_views/mg_components/Comment.vue'
import GoodsList from '../manager_views/mg_components/GoodsList.vue'
import GoodsType from '../manager_views/mg_components/GoodsType.vue'
import OrderList from '../manager_views/mg_components/OrderList.vue'
import Charts from '../manager_views/mg_components/Charts.vue'
import AddGoods from '../manager_views/mg_components/AddGoods.vue'
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect:'/main'
  },
  {
    path: '/main',
    name: 'Main',
    component: Main,
    redirect: '/main/home',
    children: [
      { path: 'home', name: 'Home', component: Home },
      { path: 'details', name: 'Details', component: Details },
      { path: 'kind', name: 'Kind', component: Kind },
      {
        path: 'personal', name: 'Personal', redirect: '/main/personal/allOrder', component: Personal,
        children: [
          { path: 'allOrder', name: 'AllOrder', component: AllOrder },
          { path: 'undelivered', name: 'Undelivered', component: Undelivered },
          { path: 'unpaid', name: 'Unpaid', component: Unpaid },
          { path: 'delivered', name: 'Delivered', component: Delivered },
          { path: 'received', name: 'Received', component: Received },
          { path: 'userInfo', name: 'UserInfo', component: UserInfo },
          { path: 'address', name: 'Address', component: Address }
        ]
      },
      {
        path: 'cart', name: 'Cart', component: Cart,redirect:'/main/cart/confirm',
        children: [
          { path: 'confirm', name: 'Confirm', component: Confirm },
          {
            path: 'payment', name: 'Payment', component: Payment,
          //   beforeEnter: (to, from, next) => {
          //   // ...
          //   console.log(to,from)
            // }
          }, 
          { path: 'paySuccess', name: 'PaySuccess', component: PaySuccess }
        ]  
      }
    ]
  },
  {
    path: '/manager', name: 'Manager', component: Manager,
    children: [
      { path: 'user', name: 'User', component: User },
      {
        path: 'goodsList', name: 'GoodsList', component: GoodsList, },
      { path: 'comment', name: 'Comment', component: Comment },
      { path: 'goodsType', name: 'GoodsType', component: GoodsType },
      { path: 'orderList', name: 'OrderList', component: OrderList },
      { path: 'banner', name: 'Banner', component: Banner },
      { path: 'charts', name: 'Charts', component: Charts },
      { path: 'goodsList/addGoods', name: 'AddGoods', component: AddGoods },
      
  ]}
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from) => {
  if (to.path == '/main/home' || to.path == '/main/details' || to.path == '/main/kind') {
    return true
  } else {
    if (sessionStorage.getItem('userId')||sessionStorage.getItem('mgId')) {
      return true
    } else {
       ElMessage.error('')
        return false
    }
  }
})


export default router
